<template>
    <div>
       <mytitle>
            收藏页
        </mytitle>
       <collect v-for="item in list" :key="item.bookId" @mydel="fn" :item="item" :isShow="false"></collect>
    </div>
</template>

<script>
import title from '../../comon/title.vue'
import collect from '../list/list.vue'
    export default {
        computed:{
            list(){
                //list代表的就是后台返回的哪些数据
                return this.$store.state.collectList
            }
        },
        components: {
          mytitle:title, 
          collect,
        },
        methods: {
            fn(item){
                this.$store.commit('removeCollect',item)
            }
        }
    }
</script>

